<template>
  <div>
  <h2登陆界面></h2>
  <el-col :span="6">
  用户名:
  <el-input
  type="text"
  placeholder="请输入用户名"
  v-model="logInfo.user"
  clearable> 
</el-input>


  密码:<el-input
  type="password"
  placeholder="请输入密码"
  v-model="logInfo.psw"
  clearable>
</el-input>
  <button @click="login()">登陆</button>
  </el-col>
  </div>
</template>

<script>
const axios = require('axios');
export default {
    data() {
      return {
          logInfo:{
              user:'',
              psw:''
          }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      login(){
            this.$axios.post('/LoginController', {
            usertName: this.logInfo.user,
            pswName: this.logInfo.psw
        })
        .then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });
            }
    }
}
</script>

<style>

</style>



